function csi_featured_slider() {

	var imageSlide = $('.featured-slider > .slides');

	imageSlide.slick({

		infinite: true,
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: false,
		autoplaySpeed: 3000,
		fade: false,

        prevArrow: '<button class="slick-prev" aria-label="Previous" type="button">&larr;</button>',
        nextArrow: '<button class="slick-next" aria-label="Next" type="button">&rarr;</button>',

	});


}

csi_featured_slider();