function nt_remove_gmaps_box_style() {

    //$('.gm-style-iw').next().css({'height': '0px'}); 
    $('.gm-style-iw').next().remove(); //remove the x closer
    $('.gm-style-iw').prev().html(''); //remove style default inforwindows

}


/*
 *  new_map
 *
 *  This function will render a Google Map onto the selected jQuery element
 *
 *  @type  function
 *  @date  8/11/2013
 *  @since 4.3.0
 *
 *  @param $el (jQuery element)
 *  @return  n/a
 */

function new_map($el, $markers, $center) {

    // var
    var $markers = $('body').find($markers);

    // vars
    var args = {
        zoom                : 7,
        center              : new google.maps.LatLng(6.274993407524308, -10.748491287231445),

        mapTypeId           : google.maps.MapTypeId.ROADMAP,
        styles              : csi_map_styles,

        disableDefaultUI    : false,
        scaleControl        : true,
        scrollwheel         : false,        
        mapTypeControl      : false,
        streetViewControl   : false
        
    };


    // create map
    var map = new google.maps.Map($el[0], args);

    // add a markers reference
    map.markers = [];

    // instance the infowindow 
    infowindow = new google.maps.InfoWindow();

    // add markers
    $markers.each(function(index, value){

        add_marker( $(this), map, index );

    });

    if($center) {
        // center map
        center_map(map);
    }

    // return
    return map;

}

/*
 *  add_marker
 *
 *  This function will add a marker to the selected Google Map
 *
 *  @type  function
 *  @date  8/11/2013
 *  @since 4.3.0
 *
 *  @param $marker (jQuery element)
 *  @param map (Google Map object)
 *  @return  n/a
 */

function add_marker( $marker, map, index ) {

    var index = index || 0;

    // var
    var latlng = new google.maps.LatLng( $marker.attr('data-lat'), $marker.attr('data-lng') );


    // create marker
    var marker = new google.maps.Marker({
        position    : latlng,
        map         : map,
        icon        : $marker.attr('data-icon'),             // url
        slug        : $marker.attr('data-slug'),
        html        : $marker.html(),
    });

    // add to array
    map.markers.push( marker );

    // if marker contains HTML, add it to an infoWindow
    if( $marker.html() ) {
       
        // // pop open
        // if( index == 0 ) {
        //     infowindow.setContent($marker.html());
        //     infowindow.open( map, marker );
        //     map.setCenter(latlng);
        //     map.setZoom(7);

        //     google.maps.event.addListener(infowindow, 'domready', function(){
        //         nt_remove_gmaps_box_style();
        //     });

        // }

        // show info window when marker is clicked
        google.maps.event.addListener(marker, 'click', function() {
            infowindow.setContent($marker.html());
            infowindow.open( map, marker );
            nt_remove_gmaps_box_style();
        });
    }

}

/*
 *  center_map
 *
 *  This function will center the map, showing all markers attached to this map
 *
 *  @type  function
 *  @date  8/11/2013
 *  @since 4.3.0
 *
 *  @param map (Google Map object)
 *  @return  n/a
 */

function center_map(map) {

    // vars
    var bounds = new google.maps.LatLngBounds();

    // loop through all markers and create bounds
    $.each(map.markers, function(i, marker) {

        var latlng = new google.maps.LatLng(marker.position.lat(), marker.position.lng());

        bounds.extend(latlng);

    });

    // fit to bounds
    map.fitBounds(bounds);
    
}

/*
 *  document ready
 *
 *  This function will render each map when the document is ready (page has loaded)
 *
 *  @type  function
 *  @date  8/11/2013
 *  @since 5.0.0
 *
 *  @param n/a
 *  @return  n/a
 */
// global var
map     = null;
ftmap   = null;

$(document).ready(function() {

    if($('#csi-footer-map').length > 0) {
     
        map = new_map( $('#csi-footer-map'), '.map-marker', true );
        
    }

    if($('#featured-map').length > 0) {
     
        ftmap = new_map( $('#featured-map'), '.featured-map-marker', false );
        
    }

});
